@import url('https://fonts.googleapis.com/css2?family=Sora:wght@100..800&display=swap');

body {
    margin: 0;
    padding: 0;
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    overflow-x: hidden;
}

.hidden {
    display: none;
}

#gutenverse-wizard {
    display: flex;
    min-height: 100vh;
    width: 100vw;
    background: #E5E5E5;
    align-items: center;
    justify-content: center;
}

.wizard-wrapper {
    background: #fff;
    border-radius: 10px;
    width: 1040px;
    padding: 20px 40px;
    box-sizing: border-box;
    overflow: hidden;
}

.wizard-container-header {
    text-align: center;
    margin-bottom: 40px;

    h1 {
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 120%;
        color: #011627;
    }

    p {
        color: #40505d;
        font-size: 16px;
        font-weight: 400;
        line-height: 140%;
        max-width: 450px;
        margin: 0 auto;
    }
}

.theme-wizard-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 99999;
    width: 100%;
    background-color: #f0f0f1;
    overflow-y: scroll;
    overflow-x: hidden;
}

.theme-wizard {
    min-height: 1000px;
    background-color: #f0f0f1;
    padding: 30px;
    max-width: 1180px;
    margin: 0 auto;

    >* {
        box-sizing: border-box;
    }

    .wizard-header {
        display: flex;
        align-items: center;
        max-width: 688px;
        margin: 0 auto;

        .progress {
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            max-width: 42px;
            text-wrap: nowrap;
            flex: 0 0 auto;
            gap: 10px;

            .number {
                background-color: #E2E4E8;
                font-family: "Inter", sans-serif;
                font-weight: 500;
                font-size: 16px;
                width: 44px;
                height: 44px;
                display: flex;
                align-items: center;
                color: #7D8292;
                justify-content: center;
                border-radius: 50%;
                border: 2px solid #D0D3D9;
                margin: 0;
            }

            .progress-title {
                font-family: "Inter", sans-serif;
                font-size: 16px;
                font-weight: 500;
                text-align: left;
                color: #7d8292;
            }

            .info-notice {
                position: relative;

                .popup {
                    position: absolute;
                    top: 100%;
                    left: 0;
                    margin-top: 4px;
                    background: #f8f9ff;
                    border: 1px solid #d0d5f5;
                    padding: 6px 10px;
                    font-size: 12px;
                    color: #2c3e50;
                    border-radius: 4px;
                    white-space: nowrap;
                    z-index: 10;
                    display: none;
                    opacity: 0;
                    transition: opacity 0.5s ease;
                }

                &:hover {
                    .popup {
                        display: block;
                        opacity: 1;
                    }
                }

            }

            &.done {
                .number {
                    background-color: #933AFF;
                    color: white;
                    border-color: #933AFF;
                }

                .progress-title {
                    color: #0F1538;
                }
            }

            &.active {
                .number {
                    background: #9C4DFF;
                    border: 2px solid #9C4DFF;
                    color: #fff;
                }

                .progress-title {
                    color: #0F1538;
                }
            }
        }

        .line {
            height: 2px;
            background-color: #D0D3D9;
            flex: 1;
            margin: 0 10px 28px 10px;

            &.done {
                background-color: #933AFF;
            }
        }
    }

    .wizard-body {
        background-color: white;
        border: solid 1px #e3e4e6;
        border-radius: 20px;
        min-height: 300px;
        width: 100%;
        max-width: 1200px;
        margin-top: 56px;
        display: flex;
        overflow: hidden;

        .theme-install {
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            align-items: center;
            position: relative;
            width: 100%;

            .content-wrapper {
                width: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;

                .content-title {
                    width: 100%;
                    margin-top: 50px;
                    height: 38px;
                    font-family: "Inter", sans-serif;
                    font-size: 32px;
                    font-weight: 600;
                    line-height: 1.2;
                    letter-spacing: -0.64px;
                    text-align: center;
                    color: #011627;
                    margin-bottom: unset;
                    z-index: 2;
                }

                .content-desc {
                    width: 100%;
                    height: 38px;
                    font-family: "Inter", sans-serif;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 1.4;
                    text-align: center;
                    color: #40505D;
                    margin-bottom: 24px;
                    font-weight: 400;
                    font-size: 16px;
                    max-width: 448px;
                    margin-top: 12px;
                    z-index: 2;
                }

                .button-wrapper {
                    display: flex;
                    flex-direction: row;
                    gap: 16px;
                    z-index: 2;

                    .button-next {
                        cursor: pointer;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: 6px;
                        padding: 10px 24px;
                        border-radius: 5px;
                        border: solid 1px #9C4DFF;
                        background: linear-gradient(181.06deg, #9C4DFF 0.91%, #9C4DFF 124.87%);
                        font-family: "Inter", sans-serif;
                        font-size: 14px;
                        font-weight: 500;
                        text-align: center;
                        color: #fff;
                        transition: transform 0.3s ease, background 0.3s ease;

                        &:hover {
                            transform: scale(0.95);
                            background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
                        }
                    }
                }

                .content-body {
                    width: 100%;
                    display: flex;
                    gap: 20px;
                    justify-content: center;
                    align-items: center;
                    height: 245px;
                    z-index: 1;

                    .icon-container {
                        top: -30px;
                        width: 480px;
                        height: 480px;
                        position: absolute;
                        background-size: cover;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .icon-image {
                            height: 95px;
                        }
                    }
                }
            }
        }

        .theme-install,
        .upgrade-pro-wrapper,
        .template-install {
            .wizard-actions {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                border-top: solid 1px #e3e4e6;
                padding: 20px 40px;
                width: 100%;
                margin-top: 100px;
                justify-content: space-between;

                &.first-step {
                    margin-top: 150px;
                    z-index: 2;
                }

                .button-skip {
                    cursor: pointer;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    text-align: center;
                    height: 39px;
                    display: flex;
                    align-items: center;

                    p {
                        font-family: "Inter", sans-serif;
                        font-weight: 500;
                        font-size: 14px;
                        line-height: 100%;
                        letter-spacing: 0%;
                        vertical-align: middle;
                        color: #99A2A9;
                        transition: color .3 ease;

                        &:hover {
                            color: #9C4DFF;
                        }
                    }
                }

                .button-wrapper {
                    display: flex;
                    flex-direction: row;
                    gap: 16px;

                    .button-back {
                        cursor: pointer;
                        font-family: "Inter", sans-serif;
                        font-size: 14px;
                        font-weight: 500;
                        justify-content: center;
                        color: #9C4DFF;
                        display: flex;
                        gap: 8px;
                        align-items: center;
                        padding: 10px 24px;
                        border-radius: 5px;
                        border: solid 1px #9C4DFF;
                        transition: background 0.3s ease, color 0.3s ease;

                        &:hover {
                            background: #9C4DFF;
                            color: #fff;
                        }
                    }

                    .button-next {
                        cursor: pointer;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        gap: 6px;
                        padding: 10px 24px;
                        border-radius: 5px;
                        border: solid 1px #9C4DFF;
                        background: linear-gradient(181.06deg, #9C4DFF 0.91%, #9C4DFF 124.87%);
                        font-family: "Inter", sans-serif;
                        font-size: 14px;
                        font-weight: 500;
                        text-align: center;
                        color: #fff;
                        transition: transform 0.3s ease, background 0.3s ease;

                        &.first-step {
                            position: absolute;
                            left: 50%;
                            transform: translateX(-50%);
                            bottom: 159px;

                            &:hover {
                                transform: scale(0.95) translateX(-50%);
                            }
                        }

                        &:hover {
                            background: linear-gradient(181.06deg, #8F3CF8 0.91%, #9C4DFF 124.87%);
                        }
                    }
                }

                .button-done {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 8px;
                    padding: 12px 20px;
                    border-radius: 5px;
                    background-color: #99a2a9;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 500;
                    text-align: center;
                    color: #fff;
                }
            }
        }

        .theme-install {

            .title,
            .desc {
                width: 90%;
            }
        }

        .template-install {
            width: 100%;

            .wizard-actions {
                width: auto;
                margin-top: 0;
            }

            .template-title {
                padding: 50px 40px 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-wrap: wrap;
                flex-direction: column;

                >* {
                    max-width: 491px;
                }

                .content-title {
                    margin: 0;
                    width: 100%;
                    text-align: center;
                    font-family: 'Inter';
                    font-weight: 600;
                    font-size: 32px;
                    line-height: 120%;
                    color: #011627;
                }

                p {
                    width: 620px;
                    margin: 20px auto 0;
                    text-align: center;
                    color: #40505d;
                    font-family: 'Inter';
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 1.4;
                    color: #40505D;

                }

                .button-import-all {
                    cursor: pointer;
                    display: flex;
                    justify-content: flex-start;
                    align-items: flex-start;
                    gap: 5px;
                    padding: 12px 15px;
                    border-radius: 5px;
                    background-color: #933AFF;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 500;
                    text-align: left;
                    color: #fff;
                    transition: background-color 0.3s ease, color 0.3s ease;

                    &:hover {
                        background-color: #2435ec;
                    }

                    &.imported {
                        background-color: #99a2a9;
                        cursor: not-allowed;

                        &:hover {
                            background-color: #99a2a9;
                        }
                    }
                }
            }

            .no-template {
                width: 100%;
                display: flex;
                padding: 50px 40px 40px;
                justify-content: center;

                div {
                    display: flex;
                    justify-content: center;
                    flex-direction: column;
                    align-items: center;
                }

                h3 {
                    font-family: "Inter", sans-serif;
                    font-weight: 600;
                    font-size: 20px;
                    color: #00223D;
                }

                p {
                    font-family: "Inter", sans-serif;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 20px;
                    color: #4F6474;
                }
            }

            .template-list {
                padding: 50px 40px 40px;
                gap: 30px;
                max-height: 1100px;
                overflow-y: auto;
                margin: 0 auto;
                display: flex;
                flex-wrap: wrap;
                position: relative;

                &.uncomplete {
                    max-height: 600px;
                    overflow: hidden;

                    .overlay {
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                        background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgba(0, 0, 0, 0.6) 100%);
                        pointer-events: none;
                        z-index: 1;
                        display: flex;
                        justify-content: center;
                        align-items: center;

                        span {
                            color: white;
                            text-shadow: 1px 1px black;
                            font-size: larger;
                        }
                    }
                }

                .template-page {
                    width: calc(33.33% - 22px);
                    height: 350px;
                    border-radius: 8px;
                    border: solid 1px #e3e4e6;
                    background-color: #f9f9ff;
                    position: relative;
                    overflow: hidden;

                    &:hover {
                        border: 1px solid #933AFF;

                        .button-view-demo {
                            border: 1px solid #933AFF !important;
                            color: #933AFF;
                        }

                        .template-thumbnail {
                            .thumbnail-overlay {
                                opacity: 0.8;
                            }

                            .required-wrapper {
                                opacity: 1;
                            }
                        }

                    }

                    .template-thumbnail {
                        position: relative;



                        .thumbnail-overlay {
                            position: absolute;
                            top: 0;
                            width: 100%;
                            height: 100%;
                            opacity: 0;
                            transition: opacity 0.3s ease-in-out;
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            background: #000000d9;
                            backdrop-filter: blur(4px);
                            border-radius: 8px 8px 0px 0px;
                        }

                        .required-wrapper {
                            position: absolute;
                            top: 0;
                            opacity: 0;
                            width: 100%;
                            height: calc(350px * 75/100);
                            display: flex;
                            flex-direction: column;
                            justify-content: center;

                            .required-title {
                                font-family: "Inter", sans-serif;
                                font-weight: 500;
                                font-size: 14px;
                                line-height: 100%;
                                letter-spacing: 0%;
                                text-align: center;
                                vertical-align: middle;
                                color: #ffffff;
                                margin: 0;
                            }

                            .required-tier {
                                text-align: center;
                                font-family: "Inter", sans-serif;
                                font-weight: 400;
                                font-size: 14px;
                                line-height: 100%;
                                letter-spacing: 0%;
                                vertical-align: middle;
                                color: #ffffff99;
                            }
                        }
                    }

                    .pro-flag {
                        align-items: center;
                        background: #EF3AFF;
                        border-radius: 4px;
                        color: #fff;
                        cursor: pointer;
                        display: flex;
                        flex-direction: row;
                        font-family: "Inter", sans-serif;
                        font-size: 10px;
                        gap: 1px;
                        justify-content: center;
                        letter-spacing: 2%;
                        line-height: normal;
                        padding: 4px 6px 4px 6px;
                        position: absolute;
                        right: 10px;
                        top: 10px;
                        width: -moz-fit-content;
                        width: fit-content;
                        z-index: 99;
                    }

                    img {
                        width: 100%;
                    }

                    .template-page-desc {
                        position: absolute;
                        bottom: 0;
                        left: 0;
                        right: 0;
                        border-top: solid 1px #e3e4e6;
                        background-color: #f9f9ff;
                        padding: 16px;

                        .upgrade-pro-button {
                            width: calc(65% - 6px);
                            z-index: 10;
                            position: relative;
                            cursor: pointer;
                            transition: all 0.5s ease;
                            border-radius: 5px;
                            background: #EF3AFF;
                            background-origin: border-box;
                            background-clip: content-box, content-box, border-box;

                            .button-content-wrapper {
                                color: white;
                                display: flex;
                                padding: 10px 20px;
                                justify-content: center;
                                align-items: center;
                                font-size: 12px;
                                font-weight: 500;
                                gap: 6px;
                            }

                            svg,
                            span {
                                transition: all 0.5s ease;
                            }
                        }

                        .upgrade-pro-button:hover {
                            transform: scale(0.94);

                            svg,
                            span {
                                opacity: 0.75;
                            }
                        }

                        h3 {
                            margin: 0;
                            font-family: "Inter", sans-serif;
                            font-size: 16px;
                            font-weight: 500;
                            letter-spacing: -0.16px;
                            text-align: left;
                            color: #011627;
                        }

                        @media screen and (max-width: 1000px) {
                            .buttons {
                                flex-direction: column;

                                a,
                                .button-view-demo,
                                .button-import-page,
                                .button-upgrade-pro {
                                    width: auto !important;
                                }
                            }
                        }

                        .buttons {
                            display: flex;
                            gap: 12px;
                            margin-top: 16px;

                            .button-import-page {
                                cursor: pointer;
                                width: calc(50% - 6px);
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                gap: 8px;
                                padding: 10px 20px;
                                border-radius: 5px;
                                background-color: #933AFF;
                                flex-grow: 0;
                                font-family: "Inter", sans-serif;
                                font-size: 12px;
                                font-weight: 500;
                                text-align: center;
                                color: #fff;
                                transition: background-color 0.3s ease, color 0.3s ease;

                                &:hover {
                                    background-color: #781BE9;
                                }

                                &.imported {
                                    background-color: #99a2a9;
                                    cursor: not-allowed;

                                    &:hover {
                                        background-color: #99a2a9;
                                    }
                                }
                            }

                            .button-view-demo {
                                cursor: pointer;
                                width: calc(50% - 6px);
                                justify-content: center;
                                align-items: center;
                                gap: 8px;
                                padding: 10px 20px;
                                border-radius: 5px;
                                box-shadow: 0 1px 2px 0 #e3e4e6;
                                border: solid 1px #e3e4e6;
                                background-color: #fff;
                                font-family: "Inter", sans-serif;
                                font-size: 12px;
                                font-weight: 500;
                                text-align: center;
                                transition: background-color 0.3s ease, color 0.3s ease,
                                    border 0.3s ease;
                            }
                        }
                    }
                }
            }

            .template-actions {
                display: flex;
                align-items: center;
                justify-content: flex-end;
                border-top: solid 1px #e3e4e6;
                padding: 20px 50px;
                gap: 20px;

                .button-skip {
                    cursor: pointer;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 400;
                    text-align: center;
                    color: #99a2a9;
                }

                .button-back {
                    cursor: pointer;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 500;
                    justify-content: center;
                    color: #99a2a9;
                    display: flex;
                    gap: 8px;
                    align-items: center;
                }

                .button-next {
                    cursor: pointer;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 6px;
                    padding: 10px 24px;
                    border-radius: 5px;
                    border: solid 1px #933AFF;
                    background-color: #fff;
                    font-family: "Inter", sans-serif;
                    font-size: 14px;
                    font-weight: 500;
                    text-align: center;
                    color: #933AFF;

                    &:hover {
                        background: #933AFF;
                        color: #fff;
                    }
                }
            }

            .install-popup-wrapper {
                position: fixed;
                -webkit-backdrop-filter: blur(4px);
                backdrop-filter: blur(4px);
                background-color: #000c;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: 99999;

                .install-popup {
                    width: 420px;
                    background-color: white;
                    border-radius: 10px;
                    margin: 30% auto 0;

                    .popup-header {
                        display: flex;
                        align-items: center;
                        justify-content: space-between;
                        padding: 20px;
                        border-bottom: 1px solid #e6e8eb;

                        h3 {
                            margin: 0;
                        }

                        svg {
                            cursor: pointer;
                        }
                    }

                    .popup-footer {
                        border-top: 1px solid #e6e8eb;
                    }
                }
            }

            .f-demo-wrapper {
                margin-top: 30px;

                .loading {
                    margin: 250px auto;
                }

                .f-demo-heading {
                    display: none !important;
                }

                .f-demo-heading-button-wrapper {
                    display: none !important;
                }

                .f-demo-search-field {
                    display: none !important;
                }

                .f-demo-demo-inner-body {
                    display: flex;
                    gap: 30px;
                    margin-top: 50px;

                    .f-filter-sidebar {
                        display: none !important;
                    }

                    .f-demo-demo-inner {
                        width: 100%;
                    }
                }

                .demo-items-wrapper {
                    display: grid;
                    grid-template-columns: repeat(auto-fill, minmax(310px, 1fr));
                    grid-gap: 30px;
                    padding: 0 40px 40px;

                    .demo-like-wrapper {
                        width: 23px;
                        height: 23px;
                        background: #f4f5f7;
                        border-radius: 97.8208px;
                        display: inline-flex;
                        align-items: center;
                        cursor: pointer;
                        position: relative;
                        overflow: hidden;
                        transition: background 0.3s ease;

                        &.active {
                            background: #ffeaf1;
                        }

                        .demo-like {
                            cursor: pointer;
                            margin: 0 auto;

                            svg {
                                fill: #fff;
                                filter: drop-shadow(0px 2px 5px rgba(52, 69, 101, 0.08));
                                display: block;
                                transition: fill 0.3s ease;
                            }

                            &.active {
                                svg {
                                    path {
                                        fill: #ff005c;
                                    }
                                }
                            }
                        }

                        &.animate {
                            &.animate {
                                svg {
                                    animation: press 0.2s 1 linear;
                                }
                            }
                        }
                    }

                    .demo-item {
                        background: #ffffff;
                        position: relative;
                        transition: box-shadow 0.3s ease;
                        width: fit-content;
                        height: fit-content;
                        border-radius: 8px;
                        padding: 22px;
                        box-shadow: 0px 10px 20px 0px rgba(191, 197, 208, 0.3);
                        display: flex;
                        flex-direction: column;
                        border: 1px solid #D0D5DD;

                        &.favorite {
                            opacity: 0;
                            transition:
                                opacity 0.3s ease,
                                box-shadow 0.3s ease;

                            &.visible {
                                opacity: 1;
                            }
                        }

                        &:hover {
                            box-shadow: 0px 9.78208px 19.5642px rgba(191, 197, 208, 0.3);
                        }

                        .demo-item-loader {
                            display: flex;
                            position: absolute;
                            top: 50%;
                            right: 50%;
                            transform: translate(50%, -50%);

                            >div {
                                display: flex;
                            }
                        }

                        .demo-item-holder {
                            border-radius: 4px;
                            width: 300px;
                            overflow: hidden;
                            height: 300px;
                            box-shadow: 0px 1px 7px 2px #0F15380D;
                            order: 1;

                            img {
                                border-radius: 4px;
                                width: 100%;
                                display: block;
                                height: 300px;
                                object-fit: cover;
                                object-position: top center;
                            }
                        }

