.wizard-container-body {
    .wizard-item {
        display: flex;
        align-items: center;
        border: 1px solid #ddd;
        border-radius: 10px;
        padding: 15px 20px;
        cursor: pointer;
        margin-bottom: 20px;

        &.selected {
            border-color: #9C4DFF;
        }

        .wizard-item-icon {
            margin-right: 20px;
        }

        .wizard-item-content {
            flex: 1;

            h3 {
                font-weight: 300;
                font-size: 20px;
                line-height: 25px;
                margin: 5px 0 5px;
                color: #011627;

                strong {
                    font-weight: bold;
                }
            }

            p {
                color: #40505D;
                font-size: 14px;
                line-height: 140%;
                margin: 10px 0;
            }
        }

        .wizard-item-check {
            padding: 0 10px;
        }

        &.selected {
            .wizard-item-check {

                path {
                    fill: #9C4DFF;
                }
            }
        }
    }
}

.wizard-container-footer {
    margin: 40px -40px -20px;
    border-top: 1px solid #E3E4E6;

    .wizard-button {
        display: flex;
        justify-content: flex-end;
        gap: 15px;
        padding: 20px 40px;
    }

    .wizard-progress {
        position: relative;

        &.hide {
            opacity: 0;
            visibility: hidden;
            position: absolute;
        }

        .wizard-progress-wrapper {
            display: flex;
            width: 100%;
            justify-content: space-between;
            padding: 0 40px;
            box-sizing: border-box;
            line-height: 65px;
        }

        .progress-text,
        .progress-step {
            font-size: 12px;
            color: #99A2A9;
        }

        .progress-bar {
            height: 10px;
            display: block;
            width: 100%;
            background: #bbbbbb;
            position: relative;

            .progress-done {
                position: absolute;
                height: 100%;
                width: 0%;
                background: linear-gradient(90deg, #9C4DFF 0%, #5CD0DA 100%);
                transition: width 1s ease-in-out;
            }
        }
    }

    .button {
        background: #9C4DFF;
        color: #fff;
        padding: 8px 25px;
        border-radius: 20px;
        font-size: 14px;
        line-height: 16px;
        cursor: pointer;

        &.secondary {
            color: #011627;
            background: transparent;
        }
    }
}

.loading {
    margin: 0 auto;
    display: block;
    width: 28px;

    svg {
        animation: spin 2s infinite linear;
    }

    &.disabled {
        background-color: #99a2a9;
    }

    &.expire {
        background-color: #e4b200;
        color: #ffffff;
    }
}

